	module.exports = {"data":{"contentfulBlog":{"title":"Two lessons about successfully changing your behavior that are not fun.","content":{"childMarkdownRemark":{"html":"<p>When you want to make a change in your life it is important to first have a solid understanding of where you are today. This is true whether the change is associated with your health, fitness or any other aspect that is open to change. This seems like a fairly basic principle, however it is something that is easy to avoid. The self assessment process is the piece that creates uncomfortable answers, while day dreaming about what life will be like after everything has changed is inspiring and motivational. The self assessment piece requires that you take account for the behaviors that have been previously established, it also includes the the resources, skills and ability that you have available.</p>\n<p>The following is a discussion of:</p>\n<ol>\n<li>\n<p>Why you should embrace a little discomfort during your self assessment.</p>\n</li>\n<li>\n<p>Two statements about your personal assessment that are sabotaging your current progress.</p>\n</li>\n</ol>\n<p><strong>Embrace discomfort today:</strong></p>\n<p>When individuals come to me and want my support to improve running, increase their fitness or lose some weight, they nearly always ask if I can help them reach some imagined outcome. The fact that they can visualize themselves at the end of this journey is a valuable part of the process. I might argue it is a critical piece of the process. I also think that it is the easiest part of process. Most people can envision a life that is totally different than the one they have today. We can imagine what it would be like to run faster. We can imagine how it would feel to be 50 pounds lighter. The person may have low self-efficacy associated with the outcome and it may be difficult to accept that those outcomes are possible, but in those moments of inspiration, we can at least envision our faster, fitter and healthier selves.</p>\n<p>What I like to hear people talk about when they ask for support is their vision for what they want to achieve, along with acknowledgement of behaviors or habits they have identified as an opportunity for early success. Let’s look at a couple hypothetical examples:</p>\n<blockquote>\n<p>“I would like to be prepared to run a marathon in 5 months. I feel like my current running schedule is not adequate to be ready. I also have been wondering if I should add some cross training or strength training as I progress.”</p>\n</blockquote>\n<p>The reason I like this inquiry is because she has a clear goal. She acknowledges that her current program needs addressed, but is also willing to consider other ideas for her program.</p>\n<blockquote>\n<p>“I want to lose 40 pounds for my daughter’s wedding, which is in 6 months. I have been doing some exercise, however I have a habit of going to happy hour every Thursday and eat pizza with the kids at least once a week.”</p>\n</blockquote>\n<p>The value that I see in this inquiry is that the goal is specific and time bound. He also shared a couple observations he has already made about his current behavior and how it could be leading to his current status. Initially, I am much less concerned about what those specific observations are. The behaviors of focus can be addressed later in the process, the value is that he has already embraced some of the discomfort that is required when doing an honest self assessment.</p>\n<p>In both of those scenarios it would be easy to be ‘committed’ to running a marathon or losing 40 pounds. It is uncomfortable to start digging through the sacrifices that will need to be made, behaviors that will have to change and the habits that will need to be added or subtracted from our life. A willingness to embrace some of that discomfort early is essential.</p>\n<p><strong>Two statements about your personal assessment that are sabotaging your current progress.</strong></p>\n<p>When doing a self assessment, two damaging statements include the following:</p>\n<ol>\n<li>\n<p>“If only… “</p>\n</li>\n<li>\n<p>“I wish… “</p>\n</li>\n</ol>\n<p>I personally find it easy to follow up the process of doing an honest assessment with a list of excuses to justify the results of the assessment. Those excuses often begin with the words, “If only” or “I wish”, here are some commonly considered excuses:</p>\n<ul>\n<li>If only I had started doing this ten years ago, it would not be so hard to get started.</li>\n<li>I wish that I had more time to dedicate to this issue.</li>\n<li>If only I didn’t have to spend so much time doing other things.</li>\n<li>I wish that it was as easy for me as it was for her.</li>\n<li>If only I didn’t have to worry about all the other things, I could focus on this issue.</li>\n<li>I wish my body would let me do everything I needed it to do.</li>\n</ul>\n<p>The list can actually grow fairly long. The damaging part of these type of lists that we create in our minds, is that with each statement, we lose a little power and control of our personal lives. Each one of the statements I just shared give power over to someone or something else. While there may be elements of truth to all of the reasons we have, to elicit changes in our behavior and improvements in our lives, we need to focus on where we do have control. Once we optimize the all the opportunities we have complete control, it can be amazing at how much impact we have had on reaching our desired outcomes.</p>\n<p>If you are serious about making a change that will lead to a healthier and happier you, then there are some steps in the initial phases that will not be any fun. Embrace that discomfort and do an honest assessment. Then take some time and review your logical and emotional responses to that assessment. As you review those responses, if you have any “If only” or “I wish” statements, write them down and commit to taking control of your mindset. Once you’ve completed this identification and personal responsibility commitment, you will have established a great foundation for your future self.</p>"}}},"allContentfulLesson":{"edges":[{"node":{"title":"Week One","week":1,"slug":"week-one","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184/sets/happier-and-healthier-you"}},{"node":{"title":"Week Two","week":2,"slug":"week-two","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Three","week":3,"slug":"week-three","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Four","week":4,"slug":"week-four","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184/sets/happier-and-healthier-you"}},{"node":{"title":"Week Five","week":5,"slug":"week-five","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Six","week":6,"slug":"week-six","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Seven","week":7,"slug":"week-seven","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Eight","week":8,"slug":"week-eight","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Nine","week":9,"slug":"week-nine","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Ten","week":10,"slug":"week-ten","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Eleven","week":11,"slug":"week-eleven","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Twelve","week":12,"slug":"week-twelve","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}},{"node":{"title":"Week Thirteen","week":13,"slug":"week-thirteen","youtube":"https://www.youtube.com/playlist?list=PLAg5pHvM_nE3fE0QsmYRIk9z3tW5ovgEg","soundcloud":"https://soundcloud.com/user-335920184"}}]}},"pathContext":{"slug":"two-lessons-about-successfully-changing-your-behavior-that-are-not-fun"}}

